import "core-js/modules/es.array.push.js";
import { defineComponent, computed, ref } from 'vue';
import { useStore } from '@/store';
import { useRouter, useRoute } from 'vue-router';
import { pathMapToMenu } from '@/utils/map-menus'; // import { IRootState } from '@/store/types'

export default defineComponent({
  props: {
    collapse: {
      type: Boolean,
      default: false
    }
  },

  setup() {
    // store
    const store = useStore();
    const userMenus = computed(() => store.state.login.userMenus); // icon

    const filterIcon = icon => {
      const iconStr = icon.replace('el-icon-', '');
      return iconStr.charAt(0).toUpperCase() + iconStr.slice(1);
    }; // 


    const router = useRouter(); // 

    const route = useRoute();
    const currentPath = route.path; // 

    const menu = pathMapToMenu(userMenus.value, currentPath);
    const defaultValue = ref(menu.id + ''); // 

    const handleMenuItemClick = item => {
      // console.log(item)
      // 
      router.push({
        path: item.url ?? '/not-found'
      });
    };

    return {
      userMenus,
      defaultValue,
      filterIcon,
      handleMenuItemClick
    };
  }

});